<template>
	<view class="singer-index">
		<view @click="goback" class="arrow iconfont iconback"></view>
		<view class="head row verCenter" v-if="starDetail.star_infos">
			<image class="bg" :src="starDetail.star_infos.cover" mode="aspectFill" lazy-load="true" fade-show="true"></image>
			<view class="pic"><image :src="starDetail.star_infos.avatar" mode="aspectFill"></image></view>
			<view class="txt">
				<view class="row verCenter">
					<text class="t1">{{ starDetail.star_infos.name }}</text>
					<text class="icon"></text>
				</view>
				<text class="t2">{{ starDetail.star_infos.cover_title }}</text>
			</view>
		</view>
		<view class="section">
			<view class="nametext" v-if="starDetail.star_infos">
				<text class="t1">艺人简介</text>
				<view class="substance row">
					<text class="t2" :class="{ active: active }">{{ starDetail.star_infos.description }}</text>
					<text @click="toggle" class="t3" v-if="show">{{ text }}</text>
				</view>
			</view>
			<view class="list-box" v-if="starDetail.star_relation_active">
				<text class="tit">近期演出</text>
				<template v-if="starDetail.star_relation_active.length > 0">
					<navigator  hover-class="none" :url="'/pages/singer/vocalConcert?id='+item.id"  class="list row verCenter" v-for="(item, index) in starDetail.star_relation_active" :key="index">
						<view class="pic"><image :src="item.picture_and_vedio[0].url" mode="aspectFill" lazy-load="true" fade-show="true"></image></view>
						<view class="text">
							<text class="t1">{{ item.title }}</text>
							<text class="t2">{{ item.start_time }}</text>
							<text class="t2">{{ item.address }}</text>
							<view class="t3">{{ starDetail.star_infos.name }}</view>
							<view class="t4">
								<text class="t4a">¥</text>
								<text class="t4a">{{ item.price }}</text>
								<text class="t4c">起</text>
							</view>
						</view>
					</navigator>
				</template>
				<template v-else>
					<view class="no-date column rowCenter">
						<view class="bg"></view>
						<text>暂无演出</text>
					</view>
				</template>
			</view>
		</view>
	</view>
</template>

<script>
import { API } from '@/util/api.js';

export default {
	data() {
		return {
			text:'展开',
			active: false,
			show: false,
			starDetail: {},
			star_id: ''
		};
	},
	onLoad(options) {
		this.star_id = options.star_id;
	},
	onShow(options) {
		this.getData();
	},
	methods: {
		toggle() {
			this.active = !this.active;
			if(this.active){
				this.text='关闭';
			}else{
				this.text='展开';
			}
		},
		goback() {
			uni.navigateBack({
				delta: 1
			});
		},
		getData() {
			this.request(API.getStarDetail, 'GET', { star_id: this.star_id },true).then(res => {
				if (res.code === 10000) {
					this.starDetail = res.data;
					if (res.data.star_infos.description.length > 100) {
						this.show = true;
					}
				}
			});
		}
	}
};
</script>

<style lang="scss">
@import '../../assets/css/singer/index.scss';
</style>
